@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

:root {
	--mainColor: #1d1d1e;
	--mainColorLight: #2a325293;
	--secondaryColor: #db2b39;
	--textColor: wheat;
}

.navbar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 10vh;
	padding: 0 1rem;
	background-color: rgba(0, 0, 0, 0.349);
	color: var(--textColor);
	
}
.navbar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 9999; /* set a high z-index to make sure the navbar appears on top of other elements */
	/* add your other styles here */
  }

.Links {
	margin: 0 1rem;
	color: var(--textColor);
	text-decoration: none;
	cursor: url(./cursor2.png), pointer;
}

.image{
	height: 8vh;
	width: 8vh;
	background: transparent;
	cursor: url(./cursor2.png), pointer;
	position: relative;
	top: 0.6vh
}

.Links:hover {
	color: var(--secondaryColor);
}

.active{
	color: var(--secondaryColor);
}

header .nav-btn {
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
  text-align: center;
  position: relative;
  right: -13px;
  top: 4px
}

header div,
nav {
  
display: flex;
	align-items: center;
  position: absolute;
  right:0px;
  font-size: 1.25rem;
}

.header h3{
	padding-left: 10px;
}

@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;
		position: relative;
		
	}

	header nav {
		z-index: 9;
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: right;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 0.6s;
		-webkit-transition: 0.6s;
		-moz-transition: 0.6s;
		-ms-transition: 0.6s;
		-o-transition: 0.6s;
}

	header .responsive_nav {
		transform: translateY(100vh);
	}

	.nav-btn svg{
		position: relative;
	}

	nav .nav-close-btn {
		position: absolute;
    top: 15px;
    right: 10px;
    padding-top: 0px;
    font-size: 1rem;
	}

	.Links {
		font-size: 8vw;;
	}
}

